/* src/index.css */

/* 1. Importação de fontes */
@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600&family=Playfair+Display:wght@400;700&display=swap');

/* 2. Variáveis de cor */
:root {
  --primary: #4a7c59;        /* tom principal verde escuro */
  --secondary: #a8d5ba;      /* tom secundário suave */
  --accent1: #ffecd1;        /* pastel pêssego */
  --accent2: #d1e8ff;        /* pastel azul */
  --accent3: #e8d1ff;        /* pastel lilás */
  --text: #333;
  --background-overlay: rgba(250, 250, 250, 0.85);
  --card-text: #1a1a1a;
}

/* 3. Reset básico */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* 4. Corpo da página com fundo personalizado */
body {
  background: url('https://i.imgur.com/H35HX6u.png') center top/cover no-repeat fixed;
  font-family: 'Montserrat', sans-serif;
  color: var(--text);
  line-height: 1.6;
}

/* 5. Container principal translúcido para destacar conteúdo */
.container {
  background: var(--background-overlay);
  max-width: 1200px;
  margin: 0 auto;
  padding: 40px 20px;
  border-radius: 8px;
}

/* 6. Header e busca principal */
.header {
  display: flex;
  align-items: center;
  gap: 16px;
  margin-bottom: 32px;
}
.logo {
  width: 64px;
  height: 64px;
  background: var(--primary) url('https://i.imgur.com/2DgMrXb.png') center/cover no-repeat;
  border-radius: 50%;
}
.search-bar {
  flex: 1;
  display: flex;
  align-items: center;
  border: 2px solid var(--secondary);
  border-radius: 50px;
  overflow: hidden;
  transition: border-color .3s;
}
.search-bar:focus-within {
  border-color: var(--primary);
}
.search-bar input {
  flex: 1;
  border: none;
  outline: none;
  padding: 10px 16px;
  font-size: 1rem;
}
.search-bar button {
  border: none;
  background: var(--primary);
  color: #fff;
  padding: 8px 20px;
  font-weight: 500;
  cursor: pointer;
  transition: transform .1s;
}
.search-bar button:hover {
  transform: translateY(-1px);
}

/* 7. Resultado */
.result {
  display: flex;
  align-items: flex-start;
  gap: 32px;
  margin-top: 24px;
  flex-wrap: wrap;
}
.result h2 {
  font-family: 'Playfair Display', serif;
  color: var(--primary);
  font-size: 1.6rem;
  margin-bottom: 12px;
}
.result h3 {
  color: var(--primary);
  margin-top: 16px;
  font-weight: 500;
}
.result p, .result ul, .result a {
  font-size: 0.95rem;
}
.result a {
  color: var(--primary);
  text-decoration: underline;
}

/* 8. Imagem da planta */
.img {
  flex-shrink: 0;
  width: 300px;
  height: 200px;
  border-radius: 6px;
  overflow: hidden;
  box-shadow: 0 4px 8px rgba(0,0,0,0.1);
  display: flex;
  align-items: center;
  justify-content: center;
}
.img-error-msg {
  padding: 16px;
  text-align: center;
  color: var(--text);
}

/* 9. Dicionário Plantafix */
section {
  margin-top: 40px;
}
section h2 {
  font-family: 'Playfair Display', serif;
  font-size: 1.4rem;
  margin-bottom: 16px;
  color: var(--primary);
}

/* 10. Busca interna do dicionário */
.dict-search {
  padding: 10px 16px;
  border: 2px solid var(--secondary);
  border-radius: 50px;
  font-size: 1rem;
  outline: none;
  transition: border-color .3s, box-shadow .3s;
}
.dict-search:focus {
  border-color: var(--primary);
  box-shadow: 0 0 8px rgba(74,124,89,0.5);
}

/* 11. Grades de cards */
.grid-cards {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(180px, 1fr));
  gap: 16px;
  margin-top: 16px;
}
.card {
  padding: 14px;
  border-radius: 6px;
  color: var(--card-text);
  box-shadow: 0 2px 6px rgba(0,0,0,0.05);
  transition: transform .2s, box-shadow .2s;
}
.card:nth-child(3n+1) { background: var(--accent1); }
.card:nth-child(3n+2) { background: var(--accent2); }
.card:nth-child(3n)   { background: var(--accent3); }
.card:hover {
  transform: translateY(-4px);
  box-shadow: 0 6px 12px rgba(0,0,0,0.1);
}

/* 12. Botões */
button {
  margin-top: 16px;
  padding: 8px 16px;
  border: none;
  background: var(--primary);
  color: #fff;
  border-radius: 50px;
  cursor: pointer;
  transition: transform .1s;
}
button:hover {
  transform: translateY(-1px);
}

/* 13. Links úteis */
section ul {
  list-style: none;
  margin-top: 12px;
}
section ul li {
  margin-bottom: 8px;
}
section ul a {
  color: var(--primary);
  text-decoration: none;
  font-weight: 500;
}
section ul a:hover {
  color: var(--primary);
}

/* 14. Footer */
footer {
  margin-top: 48px;
  text-align: center;
  font-size: 0.85rem;
  color: var(--text);
}
footer a {
  color: var(--primary);
  text-decoration: none;
  font-weight: 500;
}

/* 15. Responsividade */
@media (max-width: 768px) {
  .result {
    flex-direction: column;
    align-items: center;
  }
  .img {
    width: 100%;
    max-width: 280px;
    margin-top: 16px;
  }
  .grid-cards {
    gap: 12px;
  }
}

